<template>
    <div class="questionDetail">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>{{key}}</span>
        </header>
        <section>
            <p>{{value}}</p>
        </section>
    </div>
</template>

<script>
    export default {
        name: "QuestionDetail",
        data(){
            return{
                key:"",
                value:"",
            }
        },
        created(){
            this.key = this.$route.query.key;
            this.axios.get("/v3/profile/explain").then(res=>{
                // console.log(res);
                let reg = new RegExp(/Caption/);
                for(let key in res){
                    if (res[key] == this.key) {
                        let Key = key.replace(reg,"Content")
                        this.value = res[Key];
                    }
                }
            })
        }
    }
</script>

<style scoped>
    .questionDetail{
        width: 100%;
        height: 100%;
        overflow-y: auto;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    section{
        font-size: .8rem;
        padding: 2.45rem .7rem 2rem;
        color: #333;
        line-height: 1.1rem;
    }
</style>